body {
  background: linear-gradient(to right, #ff5733 20%, #fc3 40%, #33ff57 60%);
  overflow: hidden;
}

.breed-select, .loader, .error {
  background-color: #d0f4f4;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

.cat-info {
  justify-content: flex-start;
  align-items: center;
  font-size: 18px;
  display: flex;
  position: relative;
}

.cat-image {
  object-fit: contain;
  width: 600px;
  height: 500px;
  position: absolute;
  top: 0;
  left: 20px;
}

.cat-info p {
  margin-left: 720px;
}

.cat-name, .cat-description, .cat-temperament {
  text-align: left;
  flex: 1;
  max-width: 30%;
  font-size: 22px;
}

.cat-name {
  font-size: 30px;
  font-weight: bolder;
}

.breed-select {
  margin-left: 20px;
}

.loader {
  color: #1d1fce;
  text-align: center;
  max-width: 200px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  font-size: 18px;
  display: block;
}

.error {
  color: red;
  text-align: center;
  max-width: 400px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  font-size: 18px;
}

